* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  header {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 15px 8% 15px 8%;
    // background-color: #f3f3f3;
    // background-image: linear-gradient(to top left, #23122b, #fcbbe2);

  }
  
  .logo-header {
    margin-right: auto;
  }
  .LogoLink-header{
      display: flex;
      align-items: center;
    //   justify-content: center;
    // padding: 20px;
    .tym-header{
        // margin-left: 20px;
        color: #f3f3f3;
        height: 60px;
    }
  }
  
  .nav__links-header {
    list-style: none;
    display: flex;
  }
  
  .nav__links-header a,
  .cta,
  .overlay__content a {
    font-family: "Montserrat", sans-serif;
    font-weight: 500;
    color: #333;
    text-decoration: none;
    margin: 0 10px;
    font-size: 18px;
  }
  
  .nav__links-header li {
    padding: 0px 20px;
    margin-top: 18px;
  }
  
  .nav__links-header li a {
    transition: all 0.3s ease 0s;
  }
  
  .nav__links-header li a:hover {
    // color: #0088a9;
  }
  
  .cta {
    margin-left: 20px;
    padding: 9px 15px;
    // background-color: rgba(0, 136, 169, 1);
    // background-color: black;
    border: none;
    border-radius: 20px;
    cursor: pointer;
    transition: all 0.3s ease 0s;
    display: flex;
  flex-direction: column;
  justify-content: space-between;
  }
  .cta .hamburger-lines .line{
    color: black;
    display: block;
    height: 7px;
    border-radius: 10px;
    transform: translate(0,-20px);

    -webkit-column-width: 100%;
  }
  .cta .hamburger-lines .line1 {
    transform-origin: 0% 0%;
    transition: transform 0.4s ease-in-out;
  }
  
  .cta .hamburger-lines .line2 {
    transition: transform 0.2s ease-in-out;
  }
  
  .cta .hamburger-lines .line3 {
    transform-origin: 0% 100%;
    transition: transform 0.4s ease-in-out;
  }
  .cta .hamburger-lines .line {
    // display: block;
    // height: 4px;
    // width: 100%;
    // border-radius: 10px;
    // background: #0e2431;
  }
  
  .cta:hover {
    // background-color: rgba(0, 136, 169, 0.8);
  }
  
  /* Mobile Nav */
  
  .menu {
    display: none;
  }
  
  .overlay {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    background-color: #f3f3f3;
    background-image: linear-gradient(to top right, #e4e5fa ,#e0e1fd,#e4ccfd,#e0e1fd, #e4e5fa,#cab6fe);

    overflow-x: hidden;
    transition: all 0.5s ease 0s;
  }
  
  .overlay--active {
    width: 100%;
  }
  
  .overlay__content {
    display: flex;
    height: 100%;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .overlay a {
    padding: 15px;
    font-size: 36px;
    display: block;
    transition: all 0.3s ease 0s;
  }
  
  .overlay a:hover,
  .overlay a:focus {
    // color: #0088a9;
  }
  .overlay .close {
    position: absolute;
    top: 20px;
    right: 45px;
    font-size: 60px;
    color: #edf0f1;
    cursor: pointer;
  }
  
  @media screen and (max-height: 450px) {
    .overlay a {
      font-size: 20px;
    }
    .overlay .close {
      font-size: 40px;
      top: 15px;
      right: 35px;
    }
  }
  
  @media only screen and (max-width: 1079px) {
    .nav__links-header,
    .cta {
      display: none;
    }
    .menu {
      display: initial;
    }
  }
.Logo-header{
    height: 50px;
}  
