.footer {
  // background-color: #f3f3f3;
  background-color: #25262a;
  color: white;
  width: 100%;
  // position: fixed;
  // bottom: 0px;
  // height: auto;
  display: grid;
  grid-template-columns: 70vw 20vw;
}
.yout{
  // width: ;
}
.fab {
  height: 38px;
  width: 40px;
  margin: 15px;
  margin-bottom: 18px;
}
.copyright {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
}
.icons {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 1176px){
  .footer{
    position: relative;
  }
}
@media (max-width: 749px) {
    .footer{
        display: grid;
        grid-template-columns: auto;
        height: 120px;
        // width: 100vw;
    }
    .copyright{
        margin-top: 15px;
        text-align: center;
    }
}
@media only screen and (max-width: 438px){
  .copyright{
    margin: 20px;

  }
  .footer{
    height: auto;
  }
}