.section1__home-page {
  display: grid;
  grid-template-columns: 53% 47%;
  // height: 80vh;
  //   padding: 20px;
  padding: 9.5rem 3rem 0;

  font-family: "Poppins", sans-serif;
  .innerp1__home-page {
    font-size: 50px;
    //   font-size: 62.5%;

    text-align: center;
    font-weight: 500;
    font-weight: bold;
    // color: #444;
    font-style: italic;
    line-height: 1.35;
    transform: translate(10px, 0);
  }
  .innerp2__home-page-2 {
    text-align: left;
    font-size: 25px;
    margin-left: 4.3rem;
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .btn--text__home-page {
    display: inline-block;
    background: none;
    font-size: 23px;
    font-family: inherit;
    font-weight: 500;
    color: lighten($color: purple, $amount: 20);
    margin-left: 4.3rem;
    border: none;
    border-bottom: 1px solid currentColor;
    // padding-bottom: 2px;
    cursor: pointer;
    transition: all 0.3s;
    text-decoration: none;
  }
  .section1parts {
    display: flex;
    align-items: center;
    // justify-content: center;
  }
  .section1-1__home-page {
    justify-content: end;
    transform: translate(0, -50px);
  }
  .section1-2__home-page {
    justify-content: center;
    transform: translate(-20px, -30px);
  }
