.leaflet-container {
  width: 80%;
  height: 85vh;
}
.mapcont {
  font-family: "Poppins", sans-serif;

  height: 100vh;
  border-top: 1px solid #f3f3f3;
  width: 100%;
  // background-color: #c2c9cc;
  display: grid;
  grid-template-columns: 45% 55%;
  .map-in {
    color: #444;
    display: flex;
    justify-content: center;
    align-items: center;

    .innerp1__home-page {
      font-size: 48px;
      //   font-size: 62.5%;

      text-align: center;
      font-weight: 500;
      font-weight: bold;
      // color: #444;
      font-style: italic;
      line-height: 1.35;
      transform: translate(-30px, 0);
    }
    .innerp2__home-page {
      text-align: left;
      font-size: 40px;
      margin-left: 4.3rem;
      margin-top: 20px;
      color: #444;
      margin-bottom: 20px;
      transform: translate(20px, 0);
    }
    .section1parts {
      display: flex;
      align-items: center;
      // justify-content: center;
    }
  }
}
.Map {
  color: white;
