// SLideshow Page
.slideshow__home {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.slider-show {
  height: 90vh;
  width: 80vw;
  // background-color: yellow;
  border: 3px solid #f3f3f3;
  border-radius: 20px;
  box-shadow: 5px 5px #f3f3f3;
}
.slideshow__home-2 {
  //   height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.slider-show-2 {
  height: 90vh;
  width: 80vw;
  // background-color: yellow;
  //   border: 3px solid #f3f3f3;
  //   border-radius: 20px;
  //   box-shadow: 5px 5px #f3f3f3;
}
.pop-up-for-blog {
  position: fixed;
  bottom: 35px;
  right: 50px;
  background: linear-gradient(to right, #f4524d 0%, #5543ca 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  cursor: pointer;
  z-index: 99999;
  h1 {
    text-decoration: underline;
  }
}
