.get_in_touch {
  max-width: 800px;
  margin: 50px auto;
  position: relative;
  box-shadow: 0 10px 30px 0px rgba(0, 0, 0, 0.1);
  padding: 30px;

  .title {
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 3px;
    font-size: 3em;
    line-height: 48px;
    padding-bottom: 20px;
    color: #5543ca;
    background: linear-gradient(to right, #f4524d 0%, #5543ca 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  .input-text:focus + .label,
  .input-text.non-empty + .label {
    transform: translateY(-24px);
  }

  .contact-form {
    .form-field {
      position: relative;
      margin: 32px 0;

      .input-text {
        display: block;
        width: 100%;
        height: 36px;
        border-width: 0 0 2px 0;
        border-color: #5543ca;
        font-size: 18px;
        line-height: 26px;
        font-weight: 400;
        &:focus {
          outline: none;
        }
      }
      .label {
        transform: translateY(-24px);
        position: absolute;
        left: 20px;
        bottom: 11px;
        font-size: 18px;
        line-height: 26px;
        font-weight: 400;
        color: #5543ca;
        cursor: text;
        text-transform: capitalize;
        transition: transform 0.2s ease-in-out;
      }
    }
    .submit-btn {
      display: inline-block;
      background-image: linear-gradient(125deg, #a72879, #064497);
      color: #fff;
      text-transform: uppercase;
      letter-spacing: 2px;
      font-size: 16px;
      padding: 8px 16px;
      border: none;
      width: 200px;
      cursor: pointer;
    }
  }
}
.join-team {
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: context-menu;
  h1 {
   text-align: center;
   margin-top: 100px;
   margin-bottom: 100px;
   width: 400px;
   background: linear-gradient(to right, #f4524d 0%, #5543ca 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    cursor: pointer;
    text-decoration: underline;
  }
  h1:hover{
    text-transform: none;
  }
}