.about-us-1page {
  h1 {
    margin-top: 40px;
    margin-left: 60px;
    font-size: 35px;
    text-align: center;
    font-family: "Poppins", sans-serif;
    transform: translate(-20px);
    margin-bottom: 10px;
  }

  .founders-about {
    display: grid;
    grid-template-columns: 27% 73%;
    padding: 30px;
    font-family: "Poppins", sans-serif;

    h2 {
      margin-top: 10px;
      font-size: 35px;
    }

    p {
      font-size: 23px;
      margin-top: 30px;
      margin-right: 80px;
    }

    img {
      background-color: black;
      height: 450px;
      margin-right: 50px;
      margin-left: 40px;
    }
  }
}
