@import url("https://fonts.googleapis.com/css?family=Coiny");
@import url("https://fonts.googleapis.com/css2?family=Tinos&display=swap");

@import url(https://fonts.googleapis.com/css?family=Oswald:400,300,700);
.container-slider-home {
  height: 650px !important;
  background-color: white;
  border-radius: 20px;
  margin: 100px;
  // margin: 100px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.head-home-slide {
  text-align: center;
  margin: 30px 0;
}
.slider__inside__container {
  opacity: 0;
  display: grid;
  // grid-template-columns: 50% 50%;
  // height: 100%;
  padding-top: 60px;
  visibility: hidden;
  // width: 0 !important;
  margin-left: 0px !important;

  position: absolute;
  // transform: translate(-100px);
  .slide__content-2-2 {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .slider__content-2 {
    // margin-right: 70px;
    // transform: translate(-20px);
    // transform: translate(310px, -20px);
    // margin-left: 200px !important;
    display: flex;
    align-items: center;
    // width: 1000px;
    // margin-left: 0 !important;
    // width: auto !important;
    // margin: 0 100px !important;
    // margin-right: 150px !important;
    justify-content: center;
  }

  .Name {
    font-size: 25px;
    text-align: center;
    margin-top: 20px;
  }
  .Para {
    margin-top: 10px;
    font-size: 20px;
    // margin-right: 40px;
    margin-bottom: 5px;
    // z-index: 99999;
  }
  .btn-slider-more {
    color: lighten($color: purple, $amount: 20);
    font-size: 20px;
  }

  .slider__image-container {
    display: flex;
    align-items: center;
    justify-content: center;
    // margin-left: 60px;
    // width: 79vw;
    width: 79vw !important;
  }

  img {
    height: 400px;
    margin-top: -10px;
    // transform: translate(280px, -30px);
  }
}
.next {
  right: 20px;
}
.prev {
  left: 20px;
}

.btn-slide {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  //   background: #f1f1f1;
  background-color: white;
  border: 1px solid rgba(34, 34, 34, 0.287);
  //   position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  position: absolute;
  margin-top: 250px;
}
.btn-slide img {
  width: 25px;
  height: 25px;
  pointer-events: none;
}
.active-anim {
  opacity: 1;
  visibility: visible;
}
.container-dots {
  position: absolute;
  margin-top: 600px !important;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
}
.dot {
  width: 20px;
  cursor: pointer;
  height: 20px;
  border-radius: 50%;
  border: 3px solid #f1f1f1;
  margin: 0 5px;
  // background: yellow;
  // background-color: #feaedf;
}
.dot.active {
  background: rgb(32, 32, 32);
}

