@import url("https://fonts.googleapis.com/css?family=Coiny");

@import url(https://fonts.googleapis.com/css?family=Oswald:400,300,700);
.slider-2-container {
  // margin-top: 10px;
  .slider__inside__container-2 {
    opacity: 0;
    display: grid;
    // grid-template-columns: 50% 50%;
    // height: 100%;
    padding-top: 60px;
    visibility: hidden;
    position: absolute;
    // transform: translate(-100px);

    .slider__content-2 {
      // margin-right: 70px;
      // transform: translate(-20px);
      // transform: translate(310px, -20px);
      // margin-left: 200px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
    .slider-2-content-inside {
      // position: absolute;
      // bottom: 20px;
      // font-family: 'Coiny', cursive;
      font-family: "Oswald";
    }
    .Para-2 {
      margin-top: 10px;
      font-size: 50px;
      // color: transparent;
      // text-shadow: 0px 0px 3px #eee, 5px 5px 2px teal, -5px -2px 2px mix(black,red,50);
      color: white;
      font-weight: 400;
      // color: black;

      font-style: italic;
      margin-right: 40px;
      margin-bottom: 5px;
      position: relative;
      top: 250px;
      // z-index: 99999;
    }
    .btn-slider-more-2 {
      color: lighten($color: purple, $amount: 20);
      font-size: 20px;
      position: relative;
      top: 250px;
      // text-b: 1px solid black;
      text-shadow: -1px 1px 0 #000, 1px 1px 0 #000, 1px -1px 0 #000,
        -1px -1px 0 #000;
    }

    .slider__image-container-2 {
      display: flex;
      align-items: center;
      justify-content: center;
      // margin-left: 60px;
      width: 79vw;
    }

    img {
      // height: 400px;
      position: absolute;
      left: 20px;
      // width: 60vw;
      z-index: -1;
      width: 96.4vw;
      // height: 86vh;
      transform: translateX(-146px);
      // left: 20px;
      // width: 100vw;
      // position: absolute;z-index: -1;
      // transform: translate(280px, -30px);
    }
  }
  .next-home {
    right: 40px;
  }
  .prev-home {
    left: 40px;
  }

  .btn-slide-2-home {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    //   background: #f1f1f1;
    background-color: white;
    border: 1px solid rgba(34, 34, 34, 0.287);
    //   position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: absolute;
    margin-top: 250px;
  }
  .btn-slide-2-home img {
    width: 25px;
    height: 25px;
    pointer-events: none;
  }
  .active-anim-2 {
    opacity: 1;
    visibility: visible;
  }
  .container-dots-2 {
    position: absolute;
    margin-top: 580px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
  }
  .dot-2 {
    width: 20px;
    cursor: pointer;
    height: 20px;
    border-radius: 50%;
    border: 3px solid #f1f1f1;
    margin: 0 5px;
    // background: yellow;
    // background-color: #feaedf;
  }
  .dot-2.active-2 {
    background: rgb(32, 32, 32);
  }
}

